    height: 20px;
    width: 100%;
    margin-bottom: 40px;
    border-bottom: 1px solid #D5DBDB ;
    padding-left: 10px;
    font-size: 13px;
    min-width: 150px !important;
}
.tittle , .input, #materiales{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 300;
    line-height: 16px;
    color: #262628;
}
