    width: 185px;
}
#materiales, #icon-material{
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
.icon-signin{
    width: 50px;
    height: 55px;
    margin-right: 10px;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
#container{
    height: 90%;
    overflow-x: hidden;
    overflow-y: hidden;
    padding: 0 !important;
}
/* Row Padre*/
.rowMe{
    margin-left: 0 !important;
    margin-right: 0 !important;
}
#back{
    font-size: 15px;
    line-height: 16px;
    padding-left: 8%;
    margin-top:10%;
    background: #fff;
    border: none
}
#row-tittle{
    width: 90%;
    margin-top: 25px;
}
#row-type{
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    width: 100%;
    height: 80px;
    padding: 20px 0px 20px 20px;
}
