    padding-left: 30px;
}
#button{
    width: 90%;
    margin-top: 25px;
    padding-left: 80px;
}

/* Elementos */
#profile{
    margin-top: 5px;
    margin-left: 30px;
    width: 50px;
    height: 50px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
#back, .text, #cool-info, #five, #row-info, .day{
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
}
#tittle{
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-weight: bold;
    font-size: 50px;
    line-height: 60px;
}
.text{
    font-size: 11px;
    line-height: 22px;
    color: #262628;
}
#icon-back{
    height:16px;
    margin-right: 5px;
}
#tela-icon{
    width: 40px;
    height: 45px;
    margin-left: 50px;
}
#mayorista{
    margin-left: 5px;
}
#recoleta{
    margin-left: 15px;
}

#icon-type{
    display: flex;
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
}
#col-info{
    width: 120px;
    padding-right: 40px;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    display: flex;
    align-items: flex-end;
    text-align: center;
    margin-left: 30px;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    border-right: 1px solid #E5E7E9  ;
}
#direction{
    font-weight: bold;
}
.day{
    z-index: 10;
    font-size: 12px;
    line-height: 14px;
}
#select-one, #select-two{
    position: absolute;
    background: #FCE76C;
    border-radius: 70px;
    width: 48px;
    height: 47px;
}
#select-one{
    -webkit-transform: translate(135px,-14px);
            transform: translate(135px,-14px);
}
#select-two{
    -webkit-transform: translate(44px,-16px);
            transform: translate(44px,-16px);
}
#button Button{
    width: 200px;
}


.navbarIcons{
    height: 50px;;
    width: 100%;
    display: flex;
    flex-direction: row wrap;
    justify-content: space-evenly;
    margin-bottom: 20px;
}

.Icon{
    margin-left: 5px;
    margin-right: 5px;
    width: 100%;
    height:auto;
}

#iconFooter{
    position: absolute;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    bottom: 0;
    z-index: 30;
    margin-left: 150px;
    height: 60px;
    width: 60px;
    border-radius: 100px;
    background: #fff;
}
#iconFooter i{
    font-size: 55px;
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
}
